import { Dialog, Transition } from "@headlessui/react";
import { Fragment } from "react";
const StarterModal = ({
  isDeletePopupOpen,
  toggleDeletePopup,
  deleteArticle,
  articleId,
}) => {
  return (
    <Transition appear show={isDeletePopupOpen} as={Fragment}>
      <Dialog
        as="div"
        className="relative z-10"
        onClose={() => toggleDeletePopup(false)}
      >
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <div className="fixed inset-0 bg-white bg-opacity-90" />
        </Transition.Child>

        <div className="fixed inset-0 overflow-y-auto">
          <div className="flex min-h-full items-center justify-center p-4 text-center">
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0 scale-95"
              enterTo="opacity-100 scale-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100 scale-100"
              leaveTo="opacity-0 scale-95"
            >
              <Dialog.Panel className="w-full max-w-md transform overflow-hidden rounded-2xl bg-white p-4 text-left align-middle shadow-xl transition-all">
                <p className="font-medium text-lg">
                  Are you sure you want to delet this article?
                </p>
                <div className="flex items-center space-x-6 mt-6">
                  <button
                    onClick={() => toggleDeletePopup(false)}
                    className="w-full border border-black rounded-md py-2"
                  >
                    no
                  </button>
                  <button
                    onClick={() => {
                      deleteArticle(articleId);
                    }}
                    className="w-full bg-black bg-opacity-90 rounded-md py-2 text-white"
                  >
                    yes
                  </button>
                </div>
              </Dialog.Panel>
            </Transition.Child>
          </div>
        </div>
      </Dialog>
    </Transition>
  );
};
export default StarterModal;
